import React, { Component } from "react";
import Logo from "../../../Images/logo.mp4";
import Firebase from "../../../Firebase/Firebase";
import { Button } from "react-bootstrap";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";
import { removeVendor } from "../../../Config/localStorage";
class AccountUnverified extends Component {
  sendVerificationLink = async () => {
    await Firebase.sendEmailVerficationLink();
  };
  logout = async () => {
    removeVendor();
    Firebase.logout();
    this.props.updateLogin(null);
  };
  render() {
    return (
      <div
        style={{ height: "100vh", background: "white" }}
        className="flex-column d-flex align-items-center justify-content-center w-100"
      >
        <video src={Logo} style={{ width: "50%" }} muted autoPlay></video>
        {this.props.vendor ? (
          !this.props.vendor.is_verified ? (
            <p className="text-muted">
              Please wait while your account is being verified by our
              administrators.
            </p>
          ) : null
        ) : null}

        {this.props.vendor ? (
          !this.props.vendor.is_email_verified ? (
            <Button onClick={this.sendVerificationLink} variant="link">
              Your email is not verified. Click here to verify your email.
            </Button>
          ) : null
        ) : null}
        <Button style={{ marginTop: 20 }} onClick={this.logout}>
          Logout
        </Button>
      </div>
    );
  }
}
export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(AccountUnverified);
