import React, { Component } from "react";
import {
  mapDispatchToProps,
  mapStateToProps
} from "../../../Config/AdminMapping";
import { connect } from "react-redux";
import Header from "../../../Components/Sidebar/Header";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Table,
  Badge,
  Button
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
import Footer from "../../../Components/Footer/Footer";
import axios from "axios";
class Notifications extends Component {
  state = {
    message: ""
  };
  sendNotifications = async () => {
    await axios.post(
      "https://fcm.googleapis.com/fcm/send",
      {
        to: "<YOUR_DEVICE_ID_TOKEN>",
        notification: { body: this.state.message },
        priority: 10
      },
      {
        headers: {
          Authorization: "AIzaSyB7qtP77Rx3HmirB5lyWJ90RYeMu4TVnyc"
        }
      }
    );
  };
  render() {
    return (
      <div>
        <Header>
          <Container>
            <Row className="justify-content-center">
              <Col xs={12}>
                <Card>
                  <Card.Header>
                    <h1>Notifications</h1>
                  </Card.Header>
                  <Card.Body>
                    <Form>
                      <Form.Group controlId="exampleForm.ControlTextarea1">
                        <Form.Label>Notifications Message</Form.Label>
                        <Form.Control
                          placeholder="Enter the Notifications Message"
                          as="textarea"
                          onChange={e => {
                            this.setState({
                              message: e.target.value
                            });
                          }}
                          rows="3"
                        />
                      </Form.Group>
                      <Button onClick={this.sendNotifications} block>
                        Send Notifications
                      </Button>
                    </Form>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>{" "}
          <Footer></Footer>
        </Header>
      </div>
    );
  }
}

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(Notifications)
);
