import React, { Component } from "react";
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";
import Geosuggest from "react-geosuggest";
const mapStyles = {
  width: "90%",
  height: "300px",
  margin: "10px auto"
};
class MapVendor extends Component {
  state = {
    selected: {
      lat: 0,
      lng: 0
    },
    hideMap: false
  };
  componentDidMount = () => {
    this.setState({
      selected: {
        lat: this.props.lat ? this.props.lat : 0,
        lng: this.props.lng ? this.props.lng : 0
      }
    });
  };
  current = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        this.setState({
          lat: position.coords.latitude,
          long: position.coords.longitude
        });
      });
    }
  };
  mapClickHandler = (t, map, coord) => {
    this.setState({
      selected: {
        lat: coord.latLng.lat(),
        lng: coord.latLng.lng()
      }
    });
    this.props.selectArea(this.state.selected);
  };
  onPlaceSelected = place => {
    if (place) {
      this.map.setState({
        currentLocation: {
          lat: place.location.lat,
          lng: place.location.lng
        }
      });
      this.setState(
        {
          selected: {
            lat: place.location.lat,
            lng: place.location.lng
          }
        },
        () => {
          this.select.focus();
          this.props.selectArea(this.state.selected);
          this.props.setAddress(place.description);
        }
      );
    }
  };
  render() {
    return (
      <>
        <div
          style={{
            width: "100%",
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <Geosuggest
            ref={ref => (this.select = ref)}
            onSuggestSelect={this.onPlaceSelected}
          />
        </div>

        <Map
          fullscreenControl
          google={this.props.google}
          zoom={14}
          style={mapStyles}
          onClick={this.mapClickHandler}
          initialCenter={{
            lat: this.props.lat ? this.props.lat : 0,
            lng: this.props.lng ? this.props.lng : 0
          }}
          ref={ref => (this.map = ref)}
        >
          <Marker
            position={{
              lat: this.state.selected.lat,
              lng: this.state.selected.lng
            }}
          />
        </Map>
      </>
    );
  }
}
export default GoogleApiWrapper({
  apiKey: "AIzaSyB7qtP77Rx3HmirB5lyWJ90RYeMu4TVnyc"
})(MapVendor);
