import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import {
  Container,
  Row,
  Col,
  Card,
  Modal,
  Button,
  Form
} from "react-bootstrap";
import "./Menu.css";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";
import Firebase from "../../../Firebase/Firebase";
import { CardBody } from "react-bootstrap/Card";
class Menu extends Component {
  state = {
    addCategoryModalVisible: false,
    addDishesModalVisible: false,
    editCategoryModalVisible: false,
    editDishesModalVisible: false,
    addCategoryName: "",
    updateCategoryName: "",
    categoryToUpdate: "",
    addDishImage: null,
    addDishName: "",
    addDishCategory: "",
    addDishPrice: 0,
    addDishIngredients: "",
    addDishMaxSubdishes: 0,
    updateDishImage: null,
    updateDishImageUrl: "",
    updateDishName: "",
    updateDishCategory: "",
    updateDishPrice: 0,
    updateDishIngredients: "",
    updateDishKey: "",
    updateDishMaxSubdishes: 0,
    addDishSideDishes: [],
    updateDishSideDishes: [],
    updateDishIsDishVisible: false
  };
  componentDidMount = () => {
    this.setState({
      addDishCategory: this.props.categories[0]
        ? this.props.categories[0].category
        : ""
    });
  };
  inputHandler = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  setAddCategoryVisible = () => {
    this.setState({
      addCategoryModalVisible: !this.state.addCategoryModalVisible
    });
  };
  setAddDishesVisible = () => {
    this.setState({
      addDishesModalVisible: !this.state.addDishesModalVisible
    });
  };
  setEditCategoryVisible = () => {
    this.setState({
      editCategoryModalVisible: !this.state.editCategoryModalVisible
    });
  };
  setEditDishesVisible = () => {
    this.setState({
      editDishesModalVisible: !this.state.editDishesModalVisible
    });
  };
  addCategory = async () => {
    if (!(this.state.addCategoryName === "")) {
      await Firebase.addCategory(
        this.state.addCategoryName,
        this.props.vendor.uid
      );
      this.setAddCategoryVisible();
      window.location.reload();
    } else {
      alert("Please fill all fields!");
    }
  };
  updateCategory = async () => {
    await Firebase.updateCategory(
      this.state.categoryToUpdate,
      this.state.updateCategoryName,
      this.props.vendor.uid
    );
    this.setEditCategoryVisible();
    window.location.reload();
  };
  addSideDish = async () => {
    const addDishSideDishes = [
      ...this.state.addDishSideDishes,
      { subdishname: "", subdishprice: 0 }
    ];
    this.setState({ addDishSideDishes });
  };
  addUpdateSideDish = async () => {
    const updateDishSideDishes = [
      ...this.state.updateDishSideDishes,
      { subdishname: "", subdishprice: 0 }
    ];
    this.setState({ updateDishSideDishes });
  };
  deleteCategory = async category_key => {
    await Firebase.deleteCategory(category_key, this.props.vendor.uid);
    window.location.reload();
  };
  deleteDish = async dish_key => {
    await Firebase.deleteDish(dish_key);
    window.location.reload();
  };
  fileInputHandler = e => {
    this.setState({
      [e.target.name]: e.target.files[0]
    });
  };
  dishFileInputHandler = e => {
    this.setState(
      {
        updateDishImage: e.target.files[0]
      },
      async () => {
        await Firebase.updateDishImage(
          this.state.updateDishImage,
          this.state.updateDishKey
        );
      }
    );
  };
  nameChangeHandlerSideDish = (e, index) => {
    const addDishSideDishes = [...this.state.addDishSideDishes];
    addDishSideDishes[index].subdishname = e.target.value;
    this.setState({
      addDishSideDishes
    });
  };
  priceChangeHandlerSideDish = (e, index) => {
    const addDishSideDishes = [...this.state.addDishSideDishes];
    addDishSideDishes[index].subdishprice = e.target.value;
    this.setState({
      addDishSideDishes
    });
  };
  nameChangeHandlerSideDishUpdate = (e, index) => {
    const updateDishSideDishes = [...this.state.updateDishSideDishes];
    updateDishSideDishes[index].subdishname = e.target.value;
    this.setState({
      updateDishSideDishes
    });
  };
  priceChangeHandlerSideDishUpdate = (e, index) => {
    const updateDishSideDishes = [...this.state.updateDishSideDishes];
    updateDishSideDishes[index].subdishprice = e.target.value;
    this.setState({
      updateDishSideDishes
    });
  };
  addDish = async () => {
    if (
      !(
        this.state.addDishName === "" ||
        this.state.addDishCategory === "" ||
        this.state.addDishIngredients === "" ||
        this.state.addDishImage === null ||
        this.state.addDishPrice === 0 ||
        this.props.vendor.uid === "" ||
        this.state.addDishMaxSubdishes === 0
      )
    ) {
      await Firebase.addDish(
        this.state.addDishName,
        this.state.addDishCategory,
        this.state.addDishIngredients,
        this.state.addDishImage,
        this.state.addDishPrice,
        this.props.vendor.uid,
        this.state.addDishMaxSubdishes,
        this.state.addDishSideDishes
      );
      window.location.reload();
    } else {
      alert("Please fill all fields!");
    }
  };
  deleteSideDish = async index => {
    const addDishSideDishes = this.state.addDishSideDishes.filter(
      (element, i) => i !== index
    );
    this.setState({
      addDishSideDishes
    });
  };
  deleteUpdateSideDish = async index => {
    const updateDishSideDishes = this.state.updateDishSideDishes.filter(
      (element, i) => i !== index
    );
    this.setState({
      updateDishSideDishes
    });
  };
  updateDish = async () => {
    await Firebase.updateDish(
      this.state.updateDishName,
      this.state.updateDishCategory,
      this.state.updateDishIngredients,
      this.state.updateDishPrice,
      this.state.updateDishKey,
      this.state.updateDishMaxSubdishes,
      this.state.updateDishSideDishes
    );
    window.location.reload();
  };
  setUpdateSideDishes = index => {
    const updateDishSideDishes = this.props.dishes[index].subdishes
      ? Object.keys(this.props.dishes[index].subdishes).map(
          e => this.props.dishes[index].subdishes[e]
        )
      : [];

    this.setState({
      updateDishSideDishes
    });
  };
  hideDish = async () => {
    await Firebase.hideDish(
      this.state.updateDishKey,
      !this.state.updateDishIsDishVisible
    );
    window.location.reload();
  };
  render() {
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Header>
                    <h1>Menu</h1>
                  </Card.Header>
                  <Card.Body>
                    <Row>
                      <Col xs="12" md="6">
                        <Card style={{ marginBottom: "5vh" }}>
                          <Card.Header draggable>
                            <h4>Menu Categories</h4>
                          </Card.Header>
                          <Card.Body>
                            {this.props.categories.map(element => {
                              return (
                                <>
                                  <Row className="menu-icons d-flex justify-content-center align-items-center">
                                    <Col xs="8">{element.category}</Col>
                                    <Col
                                      xs="2"
                                      onClick={() => {
                                        this.setState({
                                          updateCategoryName: element.category,
                                          categoryToUpdate: element.category_key
                                        });
                                        this.setEditCategoryVisible();
                                      }}
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="create-outline"
                                      ></ion-icon>
                                    </Col>
                                    <Col
                                      xs="2"
                                      onClick={() =>
                                        this.deleteCategory(
                                          element.category_key
                                        )
                                      }
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="close-circle-outline"
                                      ></ion-icon>
                                    </Col>
                                  </Row>
                                  <hr className="mb-4"></hr>
                                </>
                              );
                            })}
                            <div
                              onClick={this.setAddCategoryVisible}
                              className="menu d-flex justify-content-center align-items-center"
                            >
                              <ion-icon name="add-circle-outline"></ion-icon>
                            </div>
                          </Card.Body>
                        </Card>
                      </Col>
                      <Col xs="12" md="6">
                        <Card>
                          <Card.Header draggable>
                            <h4>Menu Dishes</h4>
                          </Card.Header>
                          <Card.Body>
                            {this.props.dishes.map((element, index) => {
                              return (
                                <>
                                  <Row className="menu-icons d-flex justify-content-center align-items-center">
                                    <Col xs="8">
                                      <div className="d-flex align-items-center w-100">
                                        <img
                                          style={{
                                            width: 25,
                                            height: 25,
                                            borderRadius: 25,
                                            objectFit: "cover"
                                          }}
                                          src={element.photo_url}
                                        ></img>
                                        <p className="ml-3 mt-3">
                                          {element.name}
                                        </p>
                                      </div>
                                    </Col>
                                    <Col
                                      xs="2"
                                      onClick={() => {
                                        this.setUpdateSideDishes(index);
                                        this.setState({
                                          updateDishCategory: element.category,
                                          updateDishPrice: element.price,
                                          updateDishImageUrl: element.photo_url,
                                          updateDishIngredients:
                                            element.ingredients,
                                          updateDishKey: element.dish_key,
                                          updateDishName: element.name,
                                          updateDishMaxSubdishes:
                                            element.maxSubdishes,
                                          updateDishIsDishVisible: element.is_dish_visible
                                            ? element.is_dish_visible
                                            : false
                                        });
                                        this.setEditDishesVisible();
                                      }}
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="create-outline"
                                      ></ion-icon>
                                    </Col>
                                    <Col
                                      xs="2"
                                      onClick={() =>
                                        this.deleteDish(element.dish_key)
                                      }
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="close-circle-outline"
                                      ></ion-icon>
                                    </Col>
                                  </Row>
                                  <hr className="mb-4"></hr>
                                </>
                              );
                            })}
                            <div
                              onClick={this.setAddDishesVisible}
                              className="menu d-flex justify-content-center align-items-center"
                            >
                              <ion-icon name="add-circle-outline"></ion-icon>
                            </div>
                          </Card.Body>
                        </Card>
                      </Col>
                    </Row>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
        <Modal
          show={this.state.addCategoryModalVisible}
          size="lg"
          onHide={this.setAddCategoryVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Add Category
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Category Name</Form.Label>
              <Form.Control
                onChange={this.inputHandler}
                name="addCategoryName"
                value={this.state.addCategoryName}
                type="text"
                placeholder="Enter Category Name"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.addCategory}>Add Category</Button>
            <Button variant="danger" onClick={this.setAddCategoryVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.editCategoryModalVisible}
          size="lg"
          onHide={this.setEditCategoryVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Update Category
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Category Name</Form.Label>
              <Form.Control
                onChange={this.inputHandler}
                name="updateCategoryName"
                value={this.state.updateCategoryName}
                type="text"
                placeholder="Enter Category Name"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.updateCategory}>Update Category</Button>
            <Button variant="danger" onClick={this.setEditCategoryVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.addDishesModalVisible}
          size="lg"
          onHide={this.setAddDishesVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Add Dish
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex w-100 flex-column align-items-center justify-content-center">
              <img
                style={{
                  width: 200,
                  objectFit: "cover",
                  height: 200,
                  borderRadius: "50%"
                }}
                src={
                  this.state.addDishImage
                    ? URL.createObjectURL(this.state.addDishImage)
                    : "https://img.pngio.com/profile-icon-png-image-free-download-searchpngcom-profile-icon-png-673_673.png"
                }
              ></img>
              <Form.Group className="mt-5" controlId="formGridAddress1">
                <Form.Control
                  className="btn btn-primary"
                  name="addDishImage"
                  accept="image/*"
                  onChange={this.fileInputHandler}
                  type="file"
                />
              </Form.Group>
            </div>

            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Name</Form.Label>
              <Form.Control
                value={this.state.addDishName}
                name="addDishName"
                onChange={this.inputHandler}
                type="text"
                placeholder="Enter Dish Name"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect1">
              <Form.Label>Category</Form.Label>
              <Form.Control
                value={this.state.addDishCategory}
                name="addDishCategory"
                onChange={this.inputHandler}
                as="select"
              >
                {this.props.categories.map((cat, index) => (
                  <option selected={index === 0} value={cat.category}>
                    {cat.category}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Price</Form.Label>
              <Form.Control
                value={this.state.addDishPrice}
                name="addDishPrice"
                onChange={this.inputHandler}
                type="number"
                placeholder="Enter Dish Price"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Max Number of Side Dishes</Form.Label>
              <Form.Control
                value={this.state.addDishMaxSubdishes}
                name="addDishMaxSubdishes"
                onChange={this.inputHandler}
                type="number"
                placeholder="Enter Max Number of Side Dishes"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
              <Form.Label>Ingredients</Form.Label>
              <Form.Control
                placeholder="Enter Ingredients"
                as="textarea"
                value={this.state.addDishIngredients}
                name="addDishIngredients"
                onChange={this.inputHandler}
                rows="3"
              />
            </Form.Group>
            <Card>
              <Card.Body>
                {this.state.addDishSideDishes.map((element, index) => {
                  return (
                    <Row>
                      <Form.Group
                        className=" col-12 col-md-5"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Sidedish Name</Form.Label>
                        <Form.Control
                          type="text"
                          onChange={e =>
                            this.nameChangeHandlerSideDish(e, index)
                          }
                          placeholder="Enter Sidedish Name"
                        />
                      </Form.Group>
                      <Form.Group
                        className=" col-12 col-md-5"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Sidedish Price</Form.Label>
                        <Form.Control
                          type="number"
                          onChange={e =>
                            this.priceChangeHandlerSideDish(e, index)
                          }
                          placeholder="Enter Sidedish Price"
                        />
                      </Form.Group>
                      <div className=" col-12 col-md-2 d-flex align-items-center justify-content-center">
                        <Button
                          onClick={() => this.deleteSideDish(index)}
                          block
                          style={{ height: "30px" }}
                        >
                          Delete
                        </Button>
                      </div>
                    </Row>
                  );
                })}
              </Card.Body>
              <Card.Footer>
                <Button onClick={() => this.addSideDish()}>
                  Add Side Dish
                </Button>
              </Card.Footer>
            </Card>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.addDish}>Add Dishes</Button>
            <Button variant="danger" onClick={this.setAddDishesVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.editDishesModalVisible}
          size="lg"
          onHide={this.setEditDishesVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Edit Dish
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex w-100 flex-column align-items-center justify-content-center">
              <img
                style={{
                  width: 200,
                  objectFit: "cover",
                  height: 200,
                  borderRadius: "50%"
                }}
                src={
                  this.state.updateDishImage
                    ? URL.createObjectURL(this.state.updateDishImage)
                    : this.state.updateDishImageUrl
                }
              ></img>
              <Form.Group className="mt-5" controlId="formGridAddress1">
                <Form.Control
                  className="btn btn-primary"
                  name="updateDishImage"
                  accept="image/*"
                  onChange={this.dishFileInputHandler}
                  type="file"
                />
              </Form.Group>
            </div>

            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Name</Form.Label>
              <Form.Control
                value={this.state.updateDishName}
                name="updateDishName"
                onChange={this.inputHandler}
                type="text"
                placeholder="Enter Dish Name"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect1">
              <Form.Label>Category</Form.Label>
              <Form.Control
                value={this.state.updateDishCategory}
                name="updateDishCategory"
                onChange={this.inputHandler}
                as="select"
              >
                {this.props.categories.map((cat, index) => (
                  <option
                    selected={this.state.updateDishCategory === cat.category}
                    value={cat.category}
                  >
                    {cat.category}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Price</Form.Label>
              <Form.Control
                value={this.state.updateDishPrice}
                name="updateDishPrice"
                onChange={this.inputHandler}
                type="number"
                placeholder="Enter Dish Price"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Max Number of Side Dishes</Form.Label>
              <Form.Control
                value={this.state.updateDishMaxSubdishes}
                name="updateDishMaxSubdishes"
                onChange={this.inputHandler}
                type="number"
                placeholder="Enter Max Number of Side Dishes"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
              <Form.Label>Ingredients</Form.Label>
              <Form.Control
                placeholder="Enter Ingredients"
                as="textarea"
                value={this.state.updateDishIngredients}
                name="updateDishIngredients"
                onChange={this.inputHandler}
                rows="3"
              />
            </Form.Group>
            <Card>
              <Card.Body>
                {this.state.updateDishSideDishes.map((element, index) => {
                  return (
                    <Row>
                      <Form.Group
                        className=" col-12 col-md-5"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Sidedish Name</Form.Label>
                        <Form.Control
                          type="text"
                          value={
                            this.state.updateDishSideDishes[index].subdishname
                          }
                          onChange={e =>
                            this.nameChangeHandlerSideDishUpdate(e, index)
                          }
                          placeholder="Enter Sidedish Name"
                        />
                      </Form.Group>
                      <Form.Group
                        className=" col-12 col-md-5"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Sidedish Price</Form.Label>
                        <Form.Control
                          type="number"
                          value={
                            this.state.updateDishSideDishes[index].subdishprice
                          }
                          onChange={e =>
                            this.priceChangeHandlerSideDishUpdate(e, index)
                          }
                          placeholder="Enter Sidedish Price"
                        />
                      </Form.Group>
                      <div className=" col-12 col-md-2 d-flex align-items-center justify-content-center">
                        <Button
                          onClick={() => this.deleteUpdateSideDish(index)}
                          block
                          style={{ height: "30px" }}
                        >
                          Delete
                        </Button>
                      </div>
                    </Row>
                  );
                })}
              </Card.Body>
              <Card.Footer>
                <Button onClick={() => this.addUpdateSideDish()}>
                  Add Side Dish
                </Button>
              </Card.Footer>
            </Card>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.updateDish}>Confirm Edit</Button>
            <Button
              variant={
                this.state.updateDishIsDishVisible ? "danger" : "success"
              }
              onClick={this.hideDish}
            >
              {this.state.updateDishIsDishVisible ? "Hide Dish" : "Unhide Dish"}
            </Button>
            <Button variant="danger" onClick={this.setEditDishesVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
export default connect(vendorMapStateToProps, vendorMapDispatchToProps)(Menu);
