import React, { Component } from "react";
import { Container, Form, Button, Modal } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Col } from "react-bootstrap";
import Logo from "../../../Images/logo.png";
import "./Login.css";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";
import Firebase from "../../../Firebase/Firebase";
import { setVendor } from "../../../Config/localStorage";
import * as firebase from "firebase";
import StyledFirebaseAuth from "react-firebaseui/StyledFirebaseAuth";

class Login extends Component {
  state = {
    email: "",
    password: "",
    forgotMail: "",
    forgotModal: false,
    phoneModal: false,
    phone: ""
  };
  uiConfig = {
    signInFlow: "popup",

    signInOptions: [Firebase.FirebasePhoneAuth],
    callbacks: {
      signInSuccessWithAuthResult: async response => {
        const user = await Firebase.getVendor(response.user.uid);
        if (user) {
          this.props.updateLogin(user);
          setVendor(user);
          window.location.reload();
        } else {
          const vendors = await Firebase.getRestaurants();
          const userFound = vendors.find(
            e => e.number === response.user.phoneNumber
          );
          if (userFound) {
            this.props.updateLogin(userFound);
            setVendor(userFound);
            window.location.reload();
          } else {
            alert("Login Unsuccessful! Please check if number is registered.");
          }
        }
      }
    }
  };
  setForgotModal = () => {
    this.setState({
      forgotModal: !this.state.forgotModal
    });
  };
  setPhoneModal = () => {
    this.setState({
      phoneModal: !this.state.phoneModal
    });
  };
  inputHandler = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  sendResetPasswordLink = async () => {
    const vendorFound = await Firebase.checkIfVendorExists(this.state.email);
    if (vendorFound) {
      await Firebase.forgotPassword(this.state.forgotMail);
      alert(
        "An email with the reset password link has been sent to your account"
      );
    } else {
      alert("No account with this email has been registered!");
    }
  };
  facebookLogin = async () => {
    const response = await Firebase.loginVendorFacebook();
    if (response.message === "User Found") {
      this.props.updateLogin(response.result);
      setVendor(response.result);
      window.location.reload();
    } else {
      this.props.updateRegistrationSocial({
        name: response.result.displayName,
        email: response.result.email,
        uid: response.result.uid,
        photoUrl: response.result.photoURL
      });
      this.props.history.push("/vendor/registersocial");
    }
  };
  googleLogin = async () => {
    const response = await Firebase.loginVendorGoogle();
    if (response.message === "User Found") {
      this.props.updateLogin(response.result);
      setVendor(response.result);
      window.location.reload();
    } else {
      this.props.updateRegistrationSocial({
        name: response.result.displayName,
        email: response.result.email,
        uid: response.result.uid,
        photoUrl: response.result.photoURL
      });
      this.props.history.push("/vendor/registersocial");
    }
  };
  login = async () => {
    const user = await Firebase.loginVendor(
      this.state.email,
      this.state.password
    );
    if (user) {
      this.props.updateLogin(user);
      setVendor(user);
      window.location.reload();
    } else {
      alert("Login Unsuccessful");
    }
  };
  render() {
    return (
      <Container fluid>
        <Row style={{ minHeight: "100vh" }}>
          <Col
            xs="12"
            md="6"
            className="d-none d-md-flex"
            style={{
              background: "white",
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <img
              style={{ width: "50%", objectFit: "contain" }}
              src={Logo}
            ></img>
          </Col>
          <Col
            xs="12"
            md="6"
            style={{
              background: "#ba0fb4",
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <Form
              style={{
                background: "#fff",
                padding: 20,
                borderRadius: 8,
                width: "90%"
              }}
            >
              <legend style={{ marginBottom: 40, textAlign: "center" }}>
                Login to your Vendor Account
              </legend>
              <Form.Group controlId="formBasicEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control
                  name="email"
                  onChange={this.inputHandler}
                  type="email"
                  placeholder="Enter email"
                />
              </Form.Group>

              <Form.Group
                style={{ marginBottom: 20 }}
                controlId="formBasicPassword"
              >
                <Form.Label>Password</Form.Label>
                <Form.Control
                  name="password"
                  onChange={this.inputHandler}
                  type="password"
                  placeholder="Password"
                />
              </Form.Group>
              <Button
                onClick={this.login}
                block
                variant="primary"
                type="button"
              >
                Login
              </Button>
              <Button
                onClick={this.setPhoneModal}
                block
                variant="primary"
                type="button"
              >
                Login By Phone Number
              </Button>

              <p onClick={this.setForgotModal} className="forgot-link">
                Forgot your password?
              </p>
              <Row className="align-middle" style={{ marginTop: 10 }}>
                <Col xs="5">
                  <hr></hr>
                </Col>
                <Col xs="2" className="text-center text-muted">
                  or
                </Col>
                <Col xs="5">
                  <hr></hr>
                </Col>
              </Row>
              <Row style={{ marginTop: 20 }}>
                {/* <Col>
                  <Button
                    onClick={this.facebookLogin}
                    block
                    className="facebook bg-primary "
                    type="button"
                  >
                    Facebook Login
                  </Button>
                </Col> */}
                <Col>
                  <Button
                    onClick={this.googleLogin}
                    block
                    className="google bg-success"
                    type="button"
                  >
                    Google Login
                  </Button>
                </Col>
              </Row>
              <hr style={{ marginTop: 30, marginBottom: 20 }}></hr>
              <p className="text-center">
                Don't have an account?{" "}
                <Link to="/vendor/register">Click here to Register</Link>
              </p>
            </Form>
          </Col>
        </Row>
        <Modal
          show={this.state.forgotModal}
          size="lg"
          onHide={this.setForgotModal}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header>
            <h4>Forgot your password</h4>
          </Modal.Header>
          <Modal.Body>
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control
                name="forgotMail"
                onChange={this.inputHandler}
                type="email"
                value={this.state.forgotMail}
                placeholder="Enter email"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.sendResetPasswordLink}>
              Send Confirmation Email
            </Button>
            <Button variant="danger" onClick={this.setForgotModal}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.phoneModal}
          size="lg"
          onHide={this.setPhoneModal}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header>
            <h4>Login with Phonenumber</h4>
          </Modal.Header>
          <Modal.Body>
            <StyledFirebaseAuth
              uiConfig={this.uiConfig}
              firebaseAuth={Firebase.FirebaseAuth()}
            />
          </Modal.Body>
          <Modal.Footer>
            <Button variant="danger" onClick={this.setPhoneModal}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </Container>
    );
  }
}
export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(withRouter(Login));
