import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import "rc-time-picker/assets/index.css";
import TimePicker from "rc-time-picker";

import {
  Container,
  Col,
  Row,
  Card,
  InputGroup,
  FormControl,
  Button,
  Form,
  Table,
  Modal
} from "react-bootstrap";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  Label
} from "recharts";
import "./Home.css";
import { connect } from "react-redux";
import {
  vendorMapDispatchToProps,
  vendorMapStateToProps
} from "../../../Config/VendorMapping";
import getMonth from "../../../Config/Date";
import Firebase from "../../../Firebase/Firebase";
import { setVendor } from "../../../Config/localStorage";
import moment from "moment";
const format = "h:mm a";
class Home extends Component {
  state = {
    available: 0,
    numberOfTables: 0,
    tableFee: 0,
    reservationFee: 0,
    addRoomModalVisible: false,
    addRoomName: "",
    addRoomDescription: "",
    addRoomTotalParticipants: 0,
    addRoomImage: null,
    addTimeSlotSlot: "",
    addTimeSlotRoom: "",
    addTimeSlotModal: false,
    editRoomDescription: "",
    editRoomKey: "",
    editRoomModal: false,
    deleteRoomModal: false,
    deleteRoomID: "",
    deleteTimeSlotModal: false,
    deleteTimeSlotId: "",
    deleteTimeSlotRoomId: "",
    addRoomTimeSlots: []
  };
  addRoom = async () => {
    if (
      this.state.addRoomName === "" ||
      this.state.addRoomImage === null ||
      this.state.addRoomDescription === ""
    ) {
      alert("Please fill all fields");
    } else {
      const room_key = await Firebase.addRoom(
        this.props.vendor.uid,
        this.state.addRoomName,
        this.state.addRoomDescription,
        this.state.addRoomImage,
        10
      );
      this.state.addRoomTimeSlots.map(async e => {
        await this.addRoomTimeSlot(room_key, e.time_slot);
      });
    }

    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
    this.setAddRoomModelVisible();
    this.clearState();
  };
  addRoomTimeSlot = async (room_key, time_slot) => {
    await Firebase.addRoomTimeSlot(this.props.vendor.uid, room_key, time_slot);
  };
  addRoomTimeSlotIder = () => {
    const addRoomTimeSlots = [...this.state.addRoomTimeSlots];
    addRoomTimeSlots.push({
      time_slot: "0:00 am"
    });
    this.setState({ addRoomTimeSlots });
  };
  deleteRoomTimeSlotIder = index => {
    const addsRoomTimeSlots = [...this.state.addRoomTimeSlots];
    const addRoomTimeSlots = addsRoomTimeSlots.filter((e, i) => i !== index);
    this.setState({ addRoomTimeSlots });
  };
  setTimeSlotIder = (index, value) => {
    let addRoomTimeSlots = [...this.state.addRoomTimeSlots];
    const isFound = addRoomTimeSlots.find(e => e.time_slot === value);
    console.log(isFound);
    if (!isFound) {
      addRoomTimeSlots[index].time_slot = value;
    }
    this.setState({ addRoomTimeSlots });
  };
  setAddRoomModelVisible = () => {
    this.setState({
      addRoomModalVisible: !this.state.addRoomModalVisible
    });
  };
  setDeleteTimeSlotModalVisible = () => {
    this.setState({
      deleteTimeSlotModal: !this.state.deleteTimeSlotModal
    });
  };
  setEditRoomModalVisible = () => {
    this.setState({
      editRoomModal: !this.state.editRoomModal
    });
  };
  setDeleteRoomModalVisible = () => {
    this.setState({
      deleteRoomModal: !this.state.deleteRoomModal
    });
  };
  hide = async (room_key, time_slot_key, value) => {
    await Firebase.hideUnhideRoomTimeSlot(
      this.props.vendor.uid,
      room_key,
      time_slot_key,
      value
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    setVendor(vendor);
    this.props.updateLogin(vendor);
  };
  delete = async (room_key, time_slot_key) => {
    await Firebase.deleteRoomTimeSlot(
      this.props.vendor.uid,
      room_key,
      time_slot_key
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    setVendor(vendor);
    this.props.updateLogin(vendor);
  };
  deleteRoom = async () => {
    await Firebase.deleteRoom(this.props.vendor.uid, this.state.deleteRoomID);
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    setVendor(vendor);
    this.props.updateLogin(vendor);
    this.setDeleteRoomModalVisible();
  };
  setAddTimeSlotModelVisible = () => {
    this.setState({
      addTimeSlotModal: !this.state.addTimeSlotModal
    });
  };
  editRoomDescription = async () => {
    await Firebase.editRoomDescription(
      this.props.vendor.uid,
      this.state.editRoomKey,
      this.state.editRoomDescription
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    setVendor(vendor);
    this.props.updateLogin(vendor);
  };
  componentDidMount = () => {
    this.setState({
      available: this.props.vendor.instant_data
        ? this.props.vendor.instant_data.available
        : 0,
      numberOfTables: this.props.vendor.instant_data
        ? this.props.vendor.instant_data.number_of_tables
        : 0,
      tableFee: this.props.vendor.instant_data
        ? this.props.vendor.instant_data.table_fee
        : 0
    });
  };
  updateRestrictOrder = async () => {
    await Firebase.restrictOrder(
      !this.props.vendor.is_restrict_order,
      this.props.vendor.uid
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
  };
  updateClose = async () => {
    await Firebase.updateReservationClose(
      this.props.vendor.uid,
      !this.props.vendor.close
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
  };
  availableChangeHandler = e => {
    const date = new Date();
    const reservationsForToday = this.props.reservations.filter(
      e =>
        e.date ===
        getMonth(date.getMonth()) +
          " " +
          date.getDate() +
          ", " +
          date.getFullYear()
    );
    if (e.target.value < reservationsForToday.length) {
      alert(
        "You can't set available reservations to less than than " +
          reservationsForToday.length
      );
      this.setState({
        available: this.props.vendor.instant_data
          ? this.props.vendor.instant_data.available
          : 0
      });
    } else {
      this.setState({
        available: e.target.value
      });
    }
  };
  submitAvailableChanges = async () => {
    await Firebase.updateAvailable(this.props.vendor.uid, this.state.available);
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
  };
  updateEvents = async () => {
    await Firebase.updateEventsInstant(
      this.props.vendor.uid,
      this.state.available,
      this.state.numberOfTables,
      this.state.tableFee
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
  };
  updateEventsReservationFee = async () => {
    await Firebase.updateEventsReservationFee(
      this.props.vendor.uid,
      this.state.reservationFee
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
  };
  inputHandler = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  handler = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  filehandler = e => {
    this.setState({
      [e.target.name]: e.target.files[0]
    });
  };
  clearState = () => {
    this.setState({
      available: 0,
      numberOfTables: 0,
      tableFee: 0,
      reservationFee: 0,
      addRoomModalVisible: false,
      addRoomName: "",
      addRoomDescription: "",
      addRoomTotalParticipants: 0,
      addRoomImage: null,
      addTimeSlotSlot: "",
      addTimeSlotRoom: "",
      addTimeSlotModal: false,
      editRoomDescription: "",
      editRoomKey: "",
      editRoomModal: false,
      deleteRoomModal: false,
      deleteRoomID: "",
      deleteTimeSlotModal: false,
      deleteTimeSlotId: "",
      deleteTimeSlotRoomId: "",
      addRoomTimeSlots: []
    });
  };
  render() {
    const date = new Date();

    const todayReservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    date.setDate(date.getDate() + 1);
    const tommorrowReservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    date.setDate(date.getDate() + 1);
    const tommorrow2Reservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    date.setDate(date.getDate() + 1);
    const tommorrow3Reservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    date.setDate(date.getDate() + 1);
    const tommorrow4Reservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    date.setDate(date.getDate() + 1);
    const tommorrow5Reservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    date.setDate(date.getDate() + 1);
    const tommorrow6Reservations = {
      name:
        getMonth(date.getMonth()) +
        " " +
        date.getDate() +
        ", " +
        date.getFullYear(),
      reservations: this.props.reservations.filter(
        e =>
          e.date ===
          getMonth(date.getMonth()) +
            " " +
            date.getDate() +
            ", " +
            date.getFullYear()
      ).length
    };
    const data = [
      todayReservations,
      tommorrowReservations,
      tommorrow2Reservations,
      tommorrow3Reservations,
      tommorrow4Reservations,
      tommorrow5Reservations,
      tommorrow6Reservations
    ];
    const ajKiDate = new Date();
    const ajKiReservations = this.props.reservations.filter(e => {
      return (
        e.date ===
        getMonth(ajKiDate.getMonth()) +
          " " +
          ajKiDate.getDate() +
          ", " +
          ajKiDate.getFullYear()
      );
    });
    let rooms = [];
    if (
      this.props.vendor.category === "Escape Rooms" &&
      this.props.vendor.instant_data
    ) {
      rooms = Object.keys(this.props.vendor.instant_data).map(element => {
        let time_slots = [];
        if (this.props.vendor.instant_data[element].time_slots) {
          time_slots = Object.keys(
            this.props.vendor.instant_data[element].time_slots
          ).map(t => {
            const reservedBy = ajKiReservations.find(reserves => {
              return (
                reserves.reservation_for ===
                  this.props.vendor.instant_data[element].name &&
                reserves.time ===
                  this.props.vendor.instant_data[element][t].time_slot
              );
            });
            return {
              ...this.props.vendor.instant_data[element].time_slots[t],
              reservedBy
            };
          });
        }
        return {
          ...this.props.vendor.instant_data[element],
          time_slots
        };
      });
    }
    const reservationsToRender = ajKiReservations.map(e => {
      const customer = this.props.customers.find(c => c.uid === e.customer_uid);
      return (
        <tr>
          <td>
            <img
              style={{
                width: 50,
                height: 50,
                borderRadius: 50,
                objectFit: "cover"
              }}
              src={
                customer && customer.photo
                  ? customer.photo
                  : "https://img.pngio.com/profile-icon-png-image-free-download-searchpngcom-profile-icon-png-673_673.png"
              }
            ></img>
          </td>
          <td colSpan="4">
            <strong>Customer Name: </strong>{" "}
            {customer ? customer.name : "Customer has been deleted"}
            <br></br>
            <strong>Reservation Time: </strong> {e.time}
            <br></br>
            <strong>Number of Attendees: </strong> {e.attandee}
            <br></br>
            <strong>Number of Tables: </strong>{" "}
            {Math.floor(Number(e.attandee / 5)) + 1}
            <br></br>
            <strong>Status of Reservation: </strong> {e.status}
            <br></br>
            <strong>Customer Phone number: </strong>{" "}
            {customer ? customer.number : "Customer has been deleted"}
            <br></br>
            <strong>Customer Email: </strong>{" "}
            {customer ? customer.email : "Customer has been deleted"}
            <br></br>
          </td>
        </tr>
      );
    });
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Header>
                    <h1>Dashboard</h1>
                  </Card.Header>
                  <Card.Body>
                    <h3 className="mt-2">Edit Reservations</h3>
                    {this.props.vendor.category === "Restaurants" ||
                    this.props.vendor.category === "Cafes" ? (
                      <>
                        <p>Number of available Reservations</p>
                        <InputGroup className="mt-3">
                          <FormControl
                            placeholder="Number available reservations for today"
                            type="number"
                            aria-label="Recipient's username"
                            aria-describedby="basic-addon2"
                            value={this.state.available}
                            onChange={this.availableChangeHandler}
                          />
                          <InputGroup.Append>
                            <Button
                              onClick={this.submitAvailableChanges}
                              variant="primary"
                            >
                              Confirm Change
                            </Button>
                          </InputGroup.Append>
                        </InputGroup>
                        <Form.Check
                          type="switch"
                          checked={this.props.vendor.is_restrict_order}
                          onClick={this.updateRestrictOrder}
                          className="mt-2 ml-1"
                          id="custom-switch"
                          label="Stop Orders"
                        />
                      </>
                    ) : null}

                    <Form.Check
                      type="switch"
                      checked={this.props.vendor.close}
                      onClick={this.updateClose}
                      className="mt-2 mb-4 ml-1"
                      id="another-switch"
                      label="Stop Reservations"
                    />
                    {this.props.vendor.category === "Events" ? (
                      <>
                        <p className="my-3">Total Tickets Available</p>
                        <InputGroup className="mt-3">
                          <FormControl
                            placeholder="Total Tickets Available"
                            type="number"
                            value={this.state.available}
                            onChange={e =>
                              this.inputHandler("available", e.target.value)
                            }
                          />
                          <InputGroup.Append>
                            <Button
                              onClick={this.updateEvents}
                              variant="primary"
                            >
                              Confirm Change
                            </Button>
                          </InputGroup.Append>
                        </InputGroup>
                        <p className="my-3">Total Tables Available</p>
                        <InputGroup className="mt-3">
                          <FormControl
                            placeholder="Total Tables Available"
                            type="number"
                            value={this.state.numberOfTables}
                            onChange={e =>
                              this.inputHandler(
                                "numberOfTables",
                                e.target.value
                              )
                            }
                          />
                          <InputGroup.Append>
                            <Button
                              onClick={this.updateEvents}
                              variant="primary"
                            >
                              Confirm Change
                            </Button>
                          </InputGroup.Append>
                        </InputGroup>
                        <p className="my-3">Table Reservation Fee</p>
                        <InputGroup className="mt-3">
                          <FormControl
                            placeholder="Table Reservation Fee"
                            type="number"
                            value={this.state.tableFee}
                            onChange={e =>
                              this.inputHandler("tableFee", e.target.value)
                            }
                          />
                          <InputGroup.Append>
                            <Button
                              onClick={this.updateEvents}
                              variant="primary"
                            >
                              Confirm Change
                            </Button>
                          </InputGroup.Append>
                        </InputGroup>
                      </>
                    ) : null}
                    <hr></hr>
                    {this.props.vendor.category === "Escape Rooms" ? (
                      <>
                        <h3 className="mt-2">Upcoming Reservations</h3>
                        <Card className="mt-5">
                          <Card.Body>
                            {rooms.map(room => {
                              return (
                                <>
                                  <Row>
                                    <Col xs="8">
                                      <h4>{room.name}</h4>
                                    </Col>
                                    <Col
                                      xs="4"
                                      className="d-flex flex-row-reverse"
                                    >
                                      <Button
                                        onClick={async () => {
                                          this.setState({
                                            editRoomDescription:
                                              room.room_description,
                                            editRoomKey: room.room_key
                                          });
                                          this.setEditRoomModalVisible();
                                        }}
                                        variant="primary"
                                      >
                                        Edit
                                      </Button>
                                      <Button
                                        className="mr-2"
                                        onClick={async () => {
                                          this.setState({
                                            deleteRoomID: room.room_key
                                          });
                                          this.setDeleteRoomModalVisible();
                                        }}
                                        variant="danger"
                                      >
                                        Delete
                                      </Button>
                                    </Col>
                                  </Row>
                                  <Card className="mt-3">
                                    <Card.Body>
                                      {room.time_slots.map(time_slot => {
                                        return (
                                          <>
                                            <Row>
                                              <Col xs="12">
                                                <Row>
                                                  <Col
                                                    xs="8"
                                                    className={
                                                      time_slot.reservedBy
                                                        ? "text-danger h5"
                                                        : "text-success h5"
                                                    }
                                                  >
                                                    {time_slot.time_slot}
                                                  </Col>
                                                  <Col xs="4">
                                                    <Row className="d-flex flex-row-reverse">
                                                      <Button
                                                        onClick={async () => {
                                                          await this.hide(
                                                            room.room_key,
                                                            time_slot.time_slot_key,
                                                            !time_slot.hide
                                                          );
                                                        }}
                                                        className="mx-4"
                                                        variant={
                                                          time_slot.hide
                                                            ? "success"
                                                            : "danger"
                                                        }
                                                      >
                                                        {time_slot.hide
                                                          ? "Unhide"
                                                          : "Hide"}
                                                      </Button>
                                                      <Button
                                                        onClick={() => {
                                                          this.setState({
                                                            deleteTimeSlotId:
                                                              time_slot.time_slot_key,
                                                            deleteTimeSlotRoomId:
                                                              room.room_key
                                                          });
                                                          this.setDeleteTimeSlotModalVisible();
                                                        }}
                                                        variant="danger"
                                                      >
                                                        Delete
                                                      </Button>
                                                    </Row>
                                                  </Col>
                                                </Row>
                                              </Col>
                                            </Row>
                                            <hr></hr>
                                          </>
                                        );
                                      })}
                                      {/* <Row className="d-flex flex-row-reverse">
                                        <Button
                                          onClick={() => {
                                            this.setAddTimeSlotModelVisible();
                                            this.setState({
                                              addTimeSlotRoom: room.room_key
                                            });
                                          }}
                                          className="mx-4"
                                          variant="dark"
                                        >
                                          Add Time Slot
                                        </Button>
                                      </Row> */}
                                    </Card.Body>
                                  </Card>
                                  <hr></hr>
                                </>
                              );
                            })}

                            <Row className="d-flex flex-row-reverse">
                              <Button
                                onClick={this.setAddRoomModelVisible}
                                className="mx-4"
                                variant="dark"
                              >
                                Add Room
                              </Button>
                            </Row>
                          </Card.Body>
                        </Card>
                        <Modal
                          show={this.state.addRoomModalVisible}
                          size="lg"
                          onHide={this.setAddRoomModelVisible}
                          aria-labelledby="contained-modal-title-vcenter"
                          centered
                        >
                          <Modal.Header closeButton>
                            <Modal.Title id="contained-modal-title-vcenter">
                              Add Room
                            </Modal.Title>
                          </Modal.Header>
                          <Modal.Body>
                            <div className="d-flex w-100 flex-column align-items-center justify-content-center">
                              <img
                                style={{
                                  width: 200,
                                  objectFit: "cover",
                                  height: 200,
                                  borderRadius: "50%"
                                }}
                                src={
                                  this.state.addRoomImage
                                    ? URL.createObjectURL(
                                        this.state.addRoomImage
                                      )
                                    : "https://img.pngio.com/profile-icon-png-image-free-download-searchpngcom-profile-icon-png-673_673.png"
                                }
                              ></img>
                              <Form.Group
                                className="mt-5"
                                controlId="formGridAddress1"
                              >
                                <Form.Control
                                  className="btn btn-primary"
                                  name="addRoomImage"
                                  accept="image/*"
                                  onChange={this.filehandler}
                                  type="file"
                                />
                              </Form.Group>
                            </div>
                            <Form.Group controlId="exampleForm.ControlInput1">
                              <Form.Label>Room Name</Form.Label>
                              <Form.Control
                                onChange={this.handler}
                                name="addRoomName"
                                value={this.state.addRoomName}
                                type="text"
                                placeholder="Enter Room Name"
                              />
                            </Form.Group>
                            <Form.Group controlId="exampleForm.ControlInput1">
                              <Form.Label>Room Description</Form.Label>
                              <Form.Control
                                onChange={this.handler}
                                name="addRoomDescription"
                                value={this.state.addRoomDescription}
                                as="textarea"
                                placeholder="Enter Room Description"
                              />
                            </Form.Group>
                            <Card>
                              <Card>
                                <Card.Body>
                                  {this.state.addRoomTimeSlots.map(
                                    (e, index) => {
                                      return (
                                        <Row className="d-flex align-items-center justify-content-center">
                                          <Form.Group
                                            className=" col-12 col-md-10 d-flex align-items-center justify-content-center"
                                            controlId="exampleForm.ControlInput1"
                                          >
                                            <TimePicker
                                              showSecond={false}
                                              onChange={value => {
                                                console.log(
                                                  value.format(format)
                                                );

                                                if (value.format) {
                                                  const val = value.format(
                                                    format
                                                  );
                                                  this.setTimeSlotIder(
                                                    index,
                                                    val
                                                  );
                                                }
                                              }}
                                              value={moment(
                                                this.state.addRoomTimeSlots[
                                                  index
                                                ].time_slot,
                                                format
                                              )}
                                              placeholder="Select Time Slot"
                                              className="w-100"
                                              format={format}
                                              use12Hours
                                              inputReadOnly
                                            />
                                          </Form.Group>
                                          <div className=" col-12 col-md-2 d-flex align-items-center justify-content-center">
                                            <Button
                                              variant="danger"
                                              block
                                              onClick={() =>
                                                this.deleteRoomTimeSlotIder(
                                                  index
                                                )
                                              }
                                              style={{
                                                height: "30px",
                                                marginBottom: "10px"
                                              }}
                                            >
                                              Delete
                                            </Button>
                                          </div>
                                        </Row>
                                      );
                                    }
                                  )}
                                </Card.Body>
                              </Card>
                              <Card.Footer>
                                <Button onClick={this.addRoomTimeSlotIder}>
                                  Add Timeslot
                                </Button>
                              </Card.Footer>
                            </Card>
                          </Modal.Body>
                          <Modal.Footer>
                            <Button onClick={this.addRoom}>Add Room</Button>
                            <Button
                              variant="danger"
                              onClick={this.setAddRoomModelVisible}
                            >
                              Close
                            </Button>
                          </Modal.Footer>
                        </Modal>
                        <Modal
                          show={this.state.deleteRoomModal}
                          size="lg"
                          onHide={this.setDeleteRoomModalVisible}
                          aria-labelledby="contained-modal-title-vcenter"
                          centered
                        >
                          <Modal.Header closeButton>
                            <Modal.Title id="contained-modal-title-vcenter">
                              Are you sure you want to Delete Room
                            </Modal.Title>
                          </Modal.Header>
                          <Modal.Footer>
                            <Button onClick={this.deleteRoom}>Yes</Button>
                            <Button
                              variant="danger"
                              onClick={this.setDeleteRoomModalVisible}
                            >
                              No
                            </Button>
                          </Modal.Footer>
                        </Modal>
                        <Modal
                          show={this.state.deleteTimeSlotModal}
                          size="lg"
                          onHide={this.setDeleteTimeSlotModalVisible}
                          aria-labelledby="contained-modal-title-vcenter"
                          centered
                        >
                          <Modal.Header closeButton>
                            <Modal.Title id="contained-modal-title-vcenter">
                              Are you sure you want to Delete Timeslot?
                            </Modal.Title>
                          </Modal.Header>
                          <Modal.Footer>
                            <Button
                              onClick={async () => {
                                await this.delete(
                                  this.state.deleteTimeSlotRoomId,
                                  this.state.deleteTimeSlotId
                                );
                                this.setDeleteTimeSlotModalVisible();
                              }}
                            >
                              Yes
                            </Button>
                            <Button
                              variant="danger"
                              onClick={this.setDeleteTimeSlotModalVisible}
                            >
                              No
                            </Button>
                          </Modal.Footer>
                        </Modal>
                        <Modal
                          show={this.state.editRoomModal}
                          size="lg"
                          onHide={this.setEditRoomModalVisible}
                          aria-labelledby="contained-modal-title-vcenter"
                          centered
                        >
                          <Modal.Header closeButton>
                            <Modal.Title id="contained-modal-title-vcenter">
                              Edit Room
                            </Modal.Title>
                          </Modal.Header>
                          <Modal.Body>
                            <Form.Group controlId="exampleForm.ControlInput1">
                              <Form.Label>Room Description</Form.Label>
                              <Form.Control
                                onChange={this.handler}
                                name="editRoomDescription"
                                value={this.state.editRoomDescription}
                                as="textarea"
                                placeholder="Enter Room Description"
                              />
                            </Form.Group>
                          </Modal.Body>
                          <Modal.Footer>
                            <Button onClick={this.editRoomDescription}>
                              Edit Room
                            </Button>
                            <Button
                              variant="danger"
                              onClick={this.setEditRoomModalVisible}
                            >
                              Close
                            </Button>
                          </Modal.Footer>
                        </Modal>
                        <Modal
                          show={this.state.addTimeSlotModal}
                          size="lg"
                          onHide={this.setAddTimeSlotModelVisible}
                          aria-labelledby="contained-modal-title-vcenter"
                          centered
                        >
                          <Modal.Header closeButton>
                            <Modal.Title id="contained-modal-title-vcenter">
                              Add Room Time Slot
                            </Modal.Title>
                          </Modal.Header>
                          <Modal.Body>
                            <Form.Group controlId="exampleForm.ControlInput1">
                              <TimePicker
                                showSecond={false}
                                onChange={value =>
                                  this.setState({
                                    addTimeSlotSlot:
                                      value && value.format(format)
                                  })
                                }
                                placeholder="Select Time Slot"
                                className="w-50"
                                format={format}
                                use12Hours
                                inputReadOnly
                              />
                            </Form.Group>
                          </Modal.Body>
                          <Modal.Footer>
                            <Button onClick={this.addRoomTimeSlot}>
                              Add Room
                            </Button>
                            <Button
                              variant="danger"
                              onClick={this.setAddTimeSlotModelVisible}
                            >
                              Close
                            </Button>
                          </Modal.Footer>
                        </Modal>
                      </>
                    ) : (
                      <>
                        <h3 className="mt-2">Upcoming Reservations</h3>
                        <div
                          style={{ overflowX: "scroll", margin: "10px auto" }}
                          className="d-flex justify-content-center align-items-center p-5"
                        >
                          <BarChart width={992} height={300} data={data}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="name"></XAxis>
                            <YAxis></YAxis>
                            <Tooltip />
                            <Bar dataKey="reservations" fill="#82034d" />
                          </BarChart>
                        </div>
                        <hr></hr>
                        <h3 className="mt-2">Today's Reservations</h3>

                        <Table striped hover responsive>
                          <thead>
                            <tr>
                              <th>Customer Image</th>
                              <th colSpan="4">Description</th>
                            </tr>
                          </thead>
                          <tbody>
                            {reservationsToRender.length > 0 ? (
                              reservationsToRender
                            ) : (
                              <strong>No Reservations For Today</strong>
                            )}
                          </tbody>
                        </Table>
                      </>
                    )}
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
      </div>
    );
  }
}
export default connect(vendorMapStateToProps, vendorMapDispatchToProps)(Home);
