import React, { Component } from "react";
import {
  Container,
  Card,
  Row,
  Col,
  Form,
  Button,
  Carousel,
  Spinner
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import { connect } from "react-redux";
import Firebase from "../../../Firebase/Firebase";
import ReactCrop from "react-image-crop";
import "react-image-crop/dist/ReactCrop.css";

import Footer from "../../../Components/Footer/Footer";
import Header from "../../../Components/Sidebar/Header";
class Slideshow extends Component {
  state = {
    file: null,
    vendor_uid: "",
    isAdding: false,
    src: null,
    crop: {
      unit: "%",
      width: 30,
      aspect: 2 / 1
    },
    blobb: null,
    vendorType: "All"
  };
  handler = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  add = async () => {
    if (this.state.croppedImageUrl) {
      const fileNameSplit = this.state.blobb.name.split(".");
      const extension = fileNameSplit[fileNameSplit.length - 1];
      await Firebase.addSlideShow(
        this.state.blobb,
        extension,
        this.state.vendor_uid
      );
      window.location.reload();
    }
  };
  fileHandler = e => {
    this.setState({
      file: e.target.files[0]
    });
    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener("load", () =>
        this.setState({ src: reader.result })
      );
      reader.readAsDataURL(e.target.files[0]);
    }
  };
  delete = async uid => {
    await Firebase.deleteSlideshow(uid);
    window.location.reload();
  };
  onImageLoaded = image => {
    this.imageRef = image;
  };

  onCropComplete = crop => {
    this.makeClientCrop(crop);
  };

  onCropChange = async (crop, percentCrop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });
    this.setState({ crop });
    await this.makeClientCrop(crop);
  };

  async makeClientCrop(crop) {
    if (this.imageRef && crop.width && crop.height) {
      const croppedImageUrl = await this.getCroppedImg(
        this.imageRef,
        crop,
        "newFile.jpeg"
      );
      this.setState({ croppedImageUrl });
    }
  }

  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement("canvas");
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext("2d");

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );

    return new Promise((resolve, reject) => {
      canvas.toBlob(blob => {
        if (!blob) {
          //reject(new Error('Canvas is empty'));
          console.error("Canvas is empty");
          return;
        }
        blob.name = fileName;
        window.URL.revokeObjectURL(this.fileUrl);
        this.fileUrl = window.URL.createObjectURL(blob);
        this.setState({
          blobb: blob
        });
        resolve(this.fileUrl);
      }, "image/jpeg");
    });
  }
  render() {
    const vendors = this.props.vendors.map(e => {
      const condition =
        this.state.vendorType === "All" || this.state.vendorType === e.category;
      return condition ? <option value={e.uid}>{e.name}</option> : null;
    });
    const slideshows = this.props.slideshows.map(e => {
      const vendor = this.props.vendors.find(v => v.uid === e.vendor_uid);
      return (
        <Carousel.Item>
          <img
            style={{
              height: 500,
              objectFit: "cover"
            }}
            className="d-block w-100"
            src={e.image_url}
          />
          <Carousel.Caption
            style={{
              background: "rgba(0,0,0,0.5)",
              padding: 10,
              borderRadius: 10
            }}
          >
            <h3>{vendor ? vendor.name : ""}</h3>
            <Button
              variant="danger"
              type="button"
              style={{
                margin: 15
              }}
              onClick={() => this.delete(e.uid)}
            >
              Delete
            </Button>
          </Carousel.Caption>
        </Carousel.Item>
      );
    });
    return (
      <div>
        <Header>
          <Container>
            <Row className="justify-content-center">
              <Col xs={12}>
                <Card>
                  <Card.Header>
                    <h1>Slideshow</h1>
                  </Card.Header>
                  <Card.Body>
                    {slideshows.length > 0 ? (
                      <Carousel>{slideshows}</Carousel>
                    ) : (
                      <p>No Slideshows have been added yet</p>
                    )}
                    <hr></hr>
                    <Form>
                      <legend className="h4">Add New Banner</legend>
                      <Form.Row style={{ paddingTop: 10 }}>
                        <Form.Group as={Col} controlId="formGridCity">
                          <Form.Label>Vendor Type</Form.Label>
                          <Form.Control
                            as="select"
                            onChange={e =>
                              this.setState({ vendorType: e.target.value })
                            }
                          >
                            <option value="All">All</option>
                            <option value="Restaurants">Restaurants</option>
                            <option value="Cafes">Cafes</option>
                            <option value="Escape Rooms">Escape Rooms</option>
                            <option value="Events">Events</option>
                          </Form.Control>
                        </Form.Group>
                      </Form.Row>
                      <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>Vendor</Form.Label>
                        <Form.Control
                          onChange={e =>
                            this.handler("vendor_uid", e.target.value)
                          }
                          as="select"
                        >
                          <option disabled selected>
                            Select a Vendor
                          </option>
                          {vendors}
                        </Form.Control>
                      </Form.Group>
                      <Form.Group controlId="formGridPassword">
                        <Form.Label>Image</Form.Label>
                        <Form.Control
                          onChange={this.fileHandler}
                          type="file"
                          placeholder="Image"
                          accept="image/*"
                        />
                      </Form.Group>
                      {this.state.src && (
                        <ReactCrop
                          src={this.state.src}
                          crop={this.state.crop}
                          ruleOfThirds
                          onImageLoaded={this.onImageLoaded}
                          onComplete={this.onCropComplete}
                          onChange={this.onCropChange}
                        />
                      )}
                      {/* {this.state.croppedImageUrl && (
                        <img
                          alt="Crop"
                          style={{ maxWidth: "100%" }}
                          src={this.state.croppedImageUrl}
                        />
                      )} */}
                      <Button
                        onClick={this.add}
                        block
                        variant="primary"
                        type="button"
                      >
                        {this.state.isAdding ? (
                          <Spinner animation="border" variant="primary" />
                        ) : (
                          "Add Banner"
                        )}
                      </Button>
                    </Form>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
          <Footer></Footer>
        </Header>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Slideshow));
