import React, { Component } from "react";
import { Marker, Map, GoogleApiWrapper } from "google-maps-react";
const mapStyles = {
  width: "90%",
  height: "300px",
  margin: "10px auto"
};
class MapAdmin extends Component {
  render() {
    return (
      <Map
        google={this.props.google}
        zoom={14}
        style={mapStyles}
        initialCenter={{
          lat: this.props.lat,
          lng: this.props.lng
        }}
        ref={ref => (this.map = ref)}
      >
        <Marker
          position={{
            lat: this.props.lat,
            lng: this.props.lng
          }}
        />
      </Map>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyB7qtP77Rx3HmirB5lyWJ90RYeMu4TVnyc"
})(MapAdmin);
